import React from 'react';
import ReactSVG from 'react-svg';
import Draggable from 'gsap/Draggable';
import {Power2, TimelineMax, Back} from 'gsap';

import './Carousel.css';

const arrowUrl = 'https://gist.githubusercontent.com/martonlanga/7059d8957aafd0f8b839b150514620c4/raw/34ed282a3a8fdd07128ae52f5af7ec47dffbab64/arrow.svg';

class Carousel extends React.Component {
  constructor(props) {
    super(props);

    const itemWidth = this.props.images.length * window.screen.width;
    this.state = {
      activeId: -1,
      itemWidth
    };
  }

  componentDidMount() {
    const that = this;
    Draggable.create(this.refs.carouselTarget, {
      bounds: this.refs.carouselBound,
      type: 'x',
      zIndexBoost: false,
      edgeResistance: .9,
      dragResistance: .05,
      minimumMovement: 10,
      throwProps: true,
      onDragStart: () => console.log('Drag start.'),
      onDragEnd: function() {
        that.navigateSlide(this.getDirection('start'));
      }
    });
    this.navigateSlide('left');
  }

  navigateSlide = (direction) => {
    console.log(direction);
    const {itemWidth} = this.state;
    let {activeId} = this.state;
    const imagesLength = this.props.images.length;
    if (direction === 'right') {
      if (activeId > 0) {
        activeId--;
      }
      this.setState({activeId});
    } else {
      if (activeId < imagesLength - 1) {
        activeId++;
        console.log('change');
      }
      this.setState({activeId});
    }

    const tl = new TimelineMax({
      onComplete: () => console.log('Timeline complete.'),
      paused: true
    });

    const that = this;
    // Calc x
    const x = (-1 * (activeId * itemWidth / imagesLength));

    // Find dots
    const dots = [...this.refs.dots.children];
    const activeDots = dots.filter((dot, i) => i <= activeId);
    const nextDot = dots[activeDots.length] || null;

    // Reset
    tl.to(dots, .2, {
      backgroundColor: '#666',
      transform: 'scale(1)',
      marginLeft: 'unset'
    }, 'slide');

    // Next dot
    if (nextDot) {
      tl.to(nextDot, .4, {
          marginLeft: 'auto',
          ease: Back.EaseInOut
        }, 'slide');
    }

    // Active dots
    tl.to(activeDots, .2, {
      backgroundColor: '#fff',
      transform: 'scale(1.2)'
    }, 'slide');

    // Image Slide
    tl.to(this.refs.carouselTarget, .4, {
      x,
      ease: Power2.EaseInOut,
      onComplete: that.animateSlide
    }, 'slide');

    tl.play();
  };

  render() {
    const {images} = this.props;
    const {itemWidth, activeId} = this.state;

    return (
      <div className='carousel' ref='carouselBound' >
        <ul
          className='carousel__items'
          ref='carouselTarget'
          style={{width: itemWidth}}>
          {images.map((image, k) =>
            <li className='carousel__item' key={k}>
              <img src={image} alt='' className='carousel__img' />
            </li>)}
        </ul>

        <div className='carousel__nav'>
          <button
            className='carousel__nav-btn'
            onClick={() => this.navigateSlide('right')}
            disabled={activeId <= 0}
          >
            <ReactSVG
              // path='../svg/arrow.svg'
              path={arrowUrl}
            />
          </button>
          <button
            className='carousel__nav-btn'
            onClick={() => this.navigateSlide('left')}
            disabled={activeId >= images.length - 1}
          >
            <ReactSVG
              // path='../svg/arrow.svg'
              path={arrowUrl}
              svgStyle={{transform: 'rotate(180deg)'}}
            />
          </button>
        </div>

        <div className='carousel__dots' ref='dots'>
          {images.map((i, k) => {
            return <span key={k} className='carousel__dot' />;
          })}
        </div>
      </div>
    );
  }
}

export default Carousel;



