import React from 'react';
import ReactDOM from 'react-dom';
import Carousel from './components/Carousel';
import './index.css';

const images = [
  'https://picsum.photos/g/1920/1080/?image=952',
  'https://picsum.photos/g/1920/1080/?image=1075',
  'https://picsum.photos/g/1920/1080/?image=1048',
  'https://picsum.photos/g/1920/1080/?image=983',
];

ReactDOM.render(<Carousel images={images} />, document.getElementById('root'));



